


:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
  }



*{
    margin: 0;
    padding: 0;
}
.App {
  display: flex;
  width: 100vw;
  height: 100vh;
}

.sidebar{
  height: 100vh;
  /* background: rgb(238,103,143);
  background: linear-gradient(180deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%); */

  background: rgb(22,35,37);
  background: linear-gradient(180deg, rgba(22,35,37,1) 21%, rgba(18,58,71,1) 50%, rgba(23,139,163,1) 72%);
}



.profilepic{
  width: 150px;
  height: 150px;
  border-radius: 100px;
}

.typing-effect{
  font-family: 'Dancing Script', cursive; 
  font-size: 30px;
  font-weight: 700;
}
.content-right{
  height: 100vh;
  background-color: #EFEFEF;

}

.home{
  align-content: center;
}

nav ul
{
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}

.link{
  color: #F9F6F6
}

.link:hover{
  color: grey;
  text-decoration: none;
}
nav ul li{
  margin: 40px 0;
}

.school-card{
  width: 100%;
  padding: 0;
}

.school-card:hover{
  color: #1b1d20;
  /* background-color: #ffffff; */
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23)
}

.degree{
  font-size: 20px;
  font-weight: bold;
  padding: 10px 7px;
}

.container-education{
  width: 80%;
}
.school{
  font-size: 20px;
  color: #00A0FE;
}
.active{
  font-weight: bolder;
}
.icon-link{
  color: black;
  
}

.icon-link:hover{
  color: grey;
}


.skill-container{
  width: 80%;
}